/* =====================================================================================
Basic styles */
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Raleway", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

* {
  box-sizing: border-box; }

/* =====================================================================================
Container */
.container {
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  padding-top: 0;
  padding-right: 32px;
  padding-bottom: 0;
  padding-left: 32px; }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
