/* =====================================================================================
Visually-hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* =====================================================================================
Btn-page */
.btn-page {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 65px;
  max-width: 250px;
  width: 100%;
  font-weight: 800;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  border-radius: 65px;
  background-color: #f2890f;
  transition: all 0.1s linear; }

.btn-page:hover,
.btn-page:focus {
  background-color: #f39425; }

.btn-page:active {
  background-color: rgba(243, 148, 37, 0.7); }

/* ===============================POPAP===============================================*/
/* =====================================================================================
Your-city style */
.city-popap {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 100;
  max-width: 480px;
  width: 100%;
  padding-top: 32px;
  padding-left: 32px;
  padding-bottom: 33px;
  background: linear-gradient(0deg, #141414 0%, #1f1d1d 100%);
  border-radius: 3px;
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  overflow: auto; }

.city-popap--show {
  opacity: 1;
  visibility: visible;
  pointer-events: auto; }

.city-popap__btn-close {
  position: absolute;
  top: 39px;
  right: 29px;
  height: 18px;
  width: 18px;
  border-color: transparent;
  background-color: transparent;
  cursor: pointer; }

.city-popap__btn-svg {
  position: absolute;
  top: -1px;
  left: -1px;
  stroke: rgba(255, 255, 255, 0.5);
  transition: all 0.1 linear; }

.city-popap__btn-close:hover .city-popap__btn-svg,
.city-popap__btn-close:focus .city-popap__btn-svg {
  stroke: rgba(255, 255, 255, 0.8); }

.city-popap__btn-close:active .city-popap__btn-svg {
  stroke: white; }

.city-popap h2 {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 800;
  font-size: 32px;
  line-height: 32px; }

.city-popap__gps-text,
.city-popap__text {
  font-weight: normal;
  font-size: 17px;
  line-height: 17px;
  color: #e6e6e6; }

.city-popap__gps-text {
  position: relative;
  margin-top: 0;
  margin-bottom: 40px;
  padding-left: 30px; }

.city-popap__gps-text::after {
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  height: 20px;
  width: 20px;
  background-image: url("../img/gps-location.svg");
  background-repeat: no-repeat; }

.city-popap__text {
  margin-top: 0;
  margin-bottom: 16px; }

.city-popap__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.city-popap__list-link {
  display: inline-block;
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: rgba(255, 255, 255, 0.32);
  transition: all 0.1s linear; }

.city-popap__list-link:hover,
.city-popap__list-link:focus {
  color: white; }

.city-popap__list-link:active {
  color: rgba(255, 255, 255, 0.7); }

.city-popap__list-link--active {
  color: white;
  border-bottom: 2px solid #f2890f; }

/* =====================================================================================
Media max-width: 767px*/
@media (max-width: 767px) {
  .city-popap {
    height: 100vh;
    max-width: 100%;
    padding-right: 16px;
    padding-left: 16px; }
  .city-popap h2 {
    margin-bottom: 35px; }
  .city-popap__gps-text {
    margin-bottom: 51px;
    padding-left: 32px; }
  .city-popap__gps-text::after {
    left: 2px;
    top: -2px; }
  .city-popap__text {
    margin-bottom: 25px;
    padding-left: 2px; }
  .city-popap__list-link {
    margin-bottom: 22px;
    padding-bottom: 2px; } }

